<template>
  <div class="top">
    <div id="nav">
      <van-radio-group v-model="navradio" v-if="level==5">
          <van-radio name="1" checked-color="#15BDDB">自己</van-radio>
          <van-radio name="2" checked-color="#15BDDB">团队</van-radio>
      </van-radio-group>
    </div>

    <div class="info_wrapper" @click="showteam()" v-show="navradio=='2'">
      <div class="name">
        <span>团队</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{dl_name}}</span>
      </div>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>姓名</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入姓名" v-model="name"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>联系电话</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入联系电话" v-model="phone"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>微信号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入微信号" v-model="weixin"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>身份证号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入身份证号" v-model="user_id"/>
    </div>
  
    <div class="info_wrapper">
      <div class="name">
        <span>酒店名称</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入酒店名称" v-model="jiudian_name"/>
    </div>

    <div class="info_wrapper" @click="yiji=true">
      <div class="name">
        <span>酒店分成比</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{yiji_value}}</span>
      </div>
    </div>

    <!-- <div class="info_wrapper" @click="panduan()">
      <div class="name">
        <span>二级分成比</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请选择二级分成比" v-model="erji_value"/>
    </div> -->

    <div class="info_wrapper" @click="show=true">
      <div class="name">
        <span>所在区域</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{area}}</span>
      </div>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>详细地址</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi" @click="address=true">
        <van-icon name="location" color="#15BDDB"/>
        <span style="color:#15BDDB;">{{address_search}}</span>
      </div>
    </div>

    <div class="info_wrapper">
      <div class="name c6">
        <div>显示套餐 </div>
        <div>( 默认 ) </div>
      </div>
      <div>
        <van-radio-group id="taocan">
          <van-radio disabled shape="square" checked-color="#333">1 小时</van-radio>
          <van-radio disabled shape="square" checked-color="#333">3 小时</van-radio>
          <van-radio disabled shape="square" checked-color="#333">10 小时</van-radio>
        </van-radio-group>
      </div>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>1小时</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请填写1小时价格" v-model="h1"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>3小时</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请填写1小时价格" v-model="h3"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>10小时</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请填写1小时价格" v-model="h10"/>
    </div>

    <!-- <van-uploader id="up"   
      v-model="fileList"
      :max-count="2">
      
    </van-uploader> -->

    <!-- 图片上传 -->
    <!-- <van-uploader :after-read="onReads" accept="image/*" id="up">
      <van-button icon="photo" type="primary" size="small">上传酒店图片</van-button>
      <img :src="img_src.img_url" alt="" class="upimg">
    </van-uploader> -->

    <!-- 提交 -->
    <div class="submit">
      <button class="btn" @click="getsetin()">提交申请</button>
    </div>

    <!-- 入驻身份单选 -->
    <van-Popup  v-model="activeNames" id="shenfen">
      <strong class="title">请选择入驻类型</strong>
      <van-radio-group v-model="radio" @change="getshenfen()">
        <van-radio :name="index" v-for="(item,index) in shenfen_data" :key="index">{{item}}</van-radio>
      </van-radio-group>
    </van-Popup >

    <!-- 选择地址 -->
    <van-popup v-model="show" position="bottom">
      <van-area :area-list="site" @cancel="show=false" @confirm="changeAddress"/>
    </van-popup>

    <!-- 选择地址 -->
    <van-popup v-model="address" position="bottom" :style="{ height: '100%',width: '100%' }">
      <div class="search">
        <input type="text" placeholder="请输入搜索关键字" v-model="address_key" @blur="getaddress()" class="input">
        <div @click="getaddress()" class="se">搜索</div>
      </div>
      <div class="item" @click="address=false;getjwd(item,index)"
      v-for="(item,index) in address_data" 
      :key="index">
      <van-icon name="location" color="#15BDDB"/>{{item.district}}{{item.address}}{{item.name}}</div>
    </van-popup>

    <!-- 一级分成比 -->
    <van-popup v-model="yiji" :style="{ padding:'50px',width:'60%'}" class="yiji_">
      <div class="yiji">
        <h4 style="text-align: center;margin-bottom:10px">请选择酒店分成比</h4>
        <van-radio-group v-model="radio" @change='getyiji'>
          <van-radio name="1">10%</van-radio>
          <van-radio name="2">20%</van-radio>
          <van-radio name="3">30%</van-radio>
          <van-radio name="4">40%</van-radio>
          <van-radio name="5">50%</van-radio>
          <van-radio name="6">60%</van-radio>
          <van-radio name="7">70%</van-radio>
          <van-radio name="8">80%</van-radio>
          <!-- <van-radio name="7">70%</van-radio>
          <van-radio name="8">80%</van-radio> -->
          <!-- <van-radio name="9">90%</van-radio> -->
        </van-radio-group>
      </div>
    </van-popup>
    
    <!-- 代理列表 -->
    <van-popup v-model="showdaili" position="bottom">
      <van-picker 
      :columns="daili_list"
      @cancel="close_dl"
      @confirm="confirm_dl"
      show-toolbar/>
    </van-popup>
  </div>
</template>

<script>
import { getmap, getadd_hotel, gethotelagent_trun } from '@/axios/getData'
import { Popup, Radio, Area, RadioGroup, Icon, Toast, Uploader, Button, Dialog, Picker } from 'vant';
import util from '../utils/utils.js'  
export default {
  data(){
    return{
      level:4,
      dl_name:'请选择代理',
      dl_id:'',   // 代理id
      daili_list:[],
      showdaili:false,  //  团队弹出层
      // 权限
      result:[],
      result1:[],
      erji_list:99,
      yiji:false,
      yiji_value:'请选择酒店分成比',
      radio:'',   //  身份选择  +3    对应3 4 5
      erji:false,
      erji_value:'',
      radio1:'',

      jiudian_name:'',  // 酒店名称
      fileList: [],   //  酒店图片上传
      img_src:"",     //  上传图片的路径
      h1:'',
      h3:'',
      h10:'',
      navradio:'1',   //  单选  选择自己或团队
      address_key:'',
      address_search:'请选择详细地址',
      address:false,      //  定位
      address_data:[],    //  地理位置数据
      jd:'',              //  经纬度
      wd:'',
      mapshow:false,
      name:'',            //  用户名
      phone:'',           //  手机电话号码
      shenfen:'',         //  入驻身份 
      weixin:'',          //  微信号
      user_id:'',         //  身份证
      area:'请选择区域',   //  区域
      show:false,         //  选择地址
      // province:'',        //  省
      // city:'',            //  市
      // district:'',        //  区
      activeNames:false,
      radio:'',   //  身份选择  +3    对应3 4 5
      shenfen_data:['酒店', '分销商', '总代理'],   
      data:[],
      isshow:false,       // 
      value1: 0,
      value2: 'a',
      // option1: [
      //   { text: '全部商品', value: 0 },
      //   { text: '新款商品', value: 1 },
      //   { text: '活动商品', value: 2 }
      // ],
      // taocan:'',
      site: util.site,
    }
  },
  components: {
    vanPopup: Popup,
    vanRadio: Radio,
    vanArea: Area,
    vanRadioGroup: RadioGroup,
    vanIcon: Icon,
    vanUploader: Uploader,
    vanButton: Button,
    vanPicker: Picker
  },
  created() {
    this.level = localStorage.getItem('level');
    this.getteam();
  },
  methods:{
    // 数据提交
    getsetin(){
      if( this.name&& this.phone&& this.weixin&& this.user_id&& this.area&& this.jd&& this.wd&&this.h1&&this.h3&&this.h10&&this.jiudian_name&&this.yiji_value ){
        var data = {}
        data.types = this.navradio;    //  单选  选择 自己或者团队  默认团队
        data.username = this.name;
        data.mobile = this.phone;
        data.wx_number = this.weixin;
        data.code_id = this.user_id;
        data.region = this.area;
        data.one_level = this.yiji_value.substr(0,2);
        data.hotel_name = this.jiudian_name;
        data.jd = this.jd;
        data.wd = this.wd;
        data.one_hour = this.h1
        data.three_hour = this.h3
        data.ten_hour = this.h10
        if(this.navradio=='2'){
          data.user_ids =  this.dl_id          //  給团队绑定时添加成员uid
        }
        // console.log(data);
        // return;
        getadd_hotel(data).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            this.$router.go(-1)
          } else if(res.code==10000){
            Toast(res.msg);
            // this.$router.go(-1)
          }
        })
      } else {
        Toast('请完善入驻信息')
      }
    },
    // 代理选择弹出层
    close_dl(){
      this.showdaili = false
    },
    confirm_dl(value,index){
      this.dl_name = value
      this.dl_id = this.distributor_list[index].user_id
      this.showdaili = false      
    },
    // 代理选择弹出层
    showteam(){
      if(!this.daili_list.length){
        Dialog.confirm({
          title: '错误提示',
          message: '您还未添加任何代理',
          confirmButtonText:'添加代理',
          cancelButtonText:'返回',
          showCancelButton:true,
        }).then(() => {
          this.$router.push({path:'/add_daili'})
        }).catch(() => {
          
        });
      } else{
        this.showdaili = true
      }
    },
    // 获取一级分成比
    getyiji(a){
      this.yiji_value = a*10+'%'
      this.erji_list = 8-a
      this.yiji = false
    },
    // 获取二级分成比
    geterji(a){
      console.log(a)
      if(this.yiji_value){
        this.erji_value = a*10+'%'
        this.erji = false
      } else{
        this.erji = false
        Toast('请您先设置一级分成比!')
      }
    },
    // 先设置一级后设置二级
    panduan(){
      if(this.yiji_value&&!this.erji_list==0){
        this.erji = true
        return
      }else if(this.erji_list==99){
        this.erji = false
        Toast('请您先设置一级分成比!')
        return
      }
      if(this.erji_list==0){
        this.erji = false
        Toast('总分成不能高于80%')
        return;
      } 
    },
    // // 上传酒店图片
    // onReads(file) {
    //   let data = new FormData()
    //   data.append('file', file.file)
    //   console.log(file.file,5926)
    //   data.append('openid',this.$store.state.userInfo.openid)
    //   // console.log(data,file)
    //   this.$upImg(data).then((res) => {
    //     this.img_src = res.data;
    //     console.log(2,res.data.imgpath)
    //     // userinfo({head_pic: res.data.imgpath}).then((res) => { })
    //   })
    // },

    // 获取团队列表
    async getteam(){
      var res = await gethotelagent_trun();
      var arr1 = res.data.distributor_list
      this.distributor_list = res.data.distributor_list
      arr1.forEach(item1 => {
        this.daili_list.push(item1.username)
        console.log(this.daili_list)
      });
    },
    // 点击获取经纬度
    getjwd(item,i){
      this.jd = item.location.split(',')[0];
      this.wd = item.location.split(',')[1];
      this.address_search = item.district +'-'+item.address +'-'+item.name
    },
    // 获取详细地址  == 经纬度
    getaddress(){
      var  that = this
      if(!that.address_key){
        Toast('请输入搜索关键字');
        return;
      }
      axios.get("https://restapi.amap.com/v3/assistant/inputtips", {
        params: {
          key: "182a36b8ef5943532fa7d717f378e858",
          keywords: that.address_key
        }
      }).then(res => {
        // console.log(res)
        that.address_data = res.tips;
      });
    },
    // 获取身份
    getshenfen(){
      this.activeNames = false;
      this.shenfen = this.shenfen_data[this.radio]
    },
    // 变更地址
    changeAddress(val) {
      this.area = val[0].name +'-'+ val[1].name +'-'+ val[2].name
      this.show = false
    },
    getdata(){

    },
  },
  mounted(){

  }
}
</script>

<style scoped lang="scss">
.top{
  padding:30px 15%;
  // padding-bottom:200px;
  height:100vh;
  background: #FAFAFA;
  overflow-y: scroll;
}
  .info_wrapper {
    padding: 20px 0;
    box-sizing:border-box;
    border-bottom: 1px solid rgb(238, 238, 238);
    display: flex;
    align-items: center;
    .name{
      width:30%;
      text-align: right;
    }
    .red {
      color: #e62121;
    }
    .weizhi{
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
      background: #fff;
    }
    input {
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
    }
  }
  input::-webkit-input-placeholder{
    color:#999;
  }
  #shenfen{
    width:50%;
    padding:50px;
    border-radius: 10px;
    .title{
      text-align: center;
      display: inline-block;
      width:100%;
      margin-bottom:20px;
    }
    /deep/ .van-radio{
      margin-bottom:15px;
    }
  }
  #ditu{
    width:101%;
    height:100%;
  }
  .search{
    display: flex;
    padding:5%;
    align-items: center;
    .input{
      width:70%;
      border:1px solid #eee;
      padding:10px;
    }
    .se{
      margin-left:30px;
      padding:10px 30px;
      background: #15BDDB;
      color:#fff;
    }
  }
  .item{
    padding:25px 30px;
    border-bottom:1px solid #eee;
    display: flex;
    align-items: center;
    /deep/ .van-icon{
      margin-right:15px;
    } 
  }
  .submit{
    // position: fixed;
    // bottom:0;
    // z-index: 1;
    // text-align: center;
    padding:15px 0;
    background: #fff;
    width:100%;
    // border-top:1px solid #eee;
    .btn{
      margin-top: 20px;
      font-size: 30px;
      width:100%;
      background: #15BDDB;
      color:#fff;
      border-radius: 50px;
      padding:20px 0;
    }
  }
#taocan /deep/ .van-radio__label{
  color:#333;
}
#taocan /deep/ .van-radio{
  margin-bottom: 10px;
  margin-left: 30px;
}

/* 头部单选 */
#nav /deep/ .van-radio-group{
  display: flex;
  justify-content: space-around;
}
#up{
  padding:20px 0;
  display: flex;
  align-items: center;
}
/deep/ .van-uploader{
  margin-top: 10px;
}
/deep/ .van-radio{
  margin-bottom:10px;
}
/deep/ .van-checkbox{
  margin-bottom:10px;
}
.yiji_{
  border-radius: 10px;
}
</style>



